:root {
  --font: rgb(50, 15, 107);
}
.linearGrad {
  background: rgb(197,217,252);
  background: linear-gradient(300deg, rgba(197,217,252,0.7) 0%, rgba(236,220,243,0.7) 100%);
  height: 100vh;
  width: 100vw;
  position: fixed;
  top: 0;left:0;
  z-index: -1;
}
#hide {
  display:none;
}
#loader {
  font-size: 20px;
  display: none;
  justify-content: center; align-items: center; text-align: center; width: 100%;
  display: block; z-index: 900000; height: 200px; margin-top: -150vh; 
}
.left {
  margin-left: 12.5px;
}
.lds-defaultTwo {
position: relative;
width: 80px;
height: 80px;
transform: scale(0.6); margin-top: -25px;
}
.lds-defaultTwo div {
position: absolute;
width: 6px;
height: 6px;
background: #fff;
border-radius: 50%;
animation: lds-default 1.2s linear infinite;
}
.lds-defaultTwo div:nth-child(1) {
animation-delay: 0s;
top: 37px;
left: 66px;
}
.lds-defaultTwo div:nth-child(2) {
animation-delay: -0.1s;
top: 22px;
left: 62px;
}
.lds-defaultTwo div:nth-child(3) {
animation-delay: -0.2s;
