left: 22px;
}
.lds-defaultTwo div:nth-child(6) {
animation-delay: -0.5s;
top: 22px;
left: 11px;
}
.lds-defaultTwo div:nth-child(7) {
animation-delay: -0.6s;
top: 37px;
left: 7px;
}
.lds-defaultTwo div:nth-child(8) {
animation-delay: -0.7s;
top: 52px;
left: 11px;
}
.lds-defaultTwo div:nth-child(9) {
animation-delay: -0.8s;
top: 62px;
left: 22px;
}
.lds-defaultTwo div:nth-child(10) {
animation-delay: -0.9s;
top: 66px;
left: 37px;
}
.lds-defaultTwo div:nth-child(11) {
