@keyframes lds-default {
0%, 20%, 80%, 100% {
  transform: scale(1);
}
50% {
  transform: scale(1.5);
}
}

#fb-root {
  z-index: 90000;
  height: 60vh;
  align-items: center;
  display: flex;
}
.dash {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.hashtagTitle {
  text-transform: uppercase;
  font-size: 26px;
  font-weight: 200;
  margin-top: 50px;
}
.hashtagDesc {
  font-size: 20px;
  font-weight: 200;
  margin-top: 10px;
}
.hashtagList {
  margin-top: 50px;
}
.innerHashtagList {
  width: 500px;
  height: 400px;
  background: rgba(255, 255, 255, 0.301);
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.theListItem {
  display: flex;
  flex-direction: row;
  gap: 10px;
  width: 300px;
  justify-content: space-between;
}
.theListItem h1 {
  font-weight: 400;
  font-size: 18px;
  margin-top: 15px;
}
.theListItem h2 {
  font-weight: 200;
  font-size: 18px;
}

.btnContainLog {
  width: 100vw;
  display: flex;
  justify-content: center;
}
.btn {
  padding: 10px 20px;
  background: black;
  color: white; 
  border: none; 
  border-radius: 5px; text-transform: capitalize;
  text-align: center; font-family: poppins, sans-serif;
  font-weight: 200; outline: none; height: auto;
}


/* DASHBOARD */
.dashFlex {
  width: 100vw;
}
.btnHolder {
  right: 0;
  top: 0;
  padding-top: 50px;
  height: calc(100vh - 50px);
  float: right;
  width: 250px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap:25px;
  background: rgb(255, 252, 252, 0.6);
  position: fixed;
}
.mainSection {
  width: calc(100vw - 300px); 
  font-family: poppins;
  color: var(--font); font-size: 20px;
  padding-left: 50px;
}
.head {
  font-family: poppins; font-weight: 700; color: var(--font); font-size: 40px;
  margin-top: 50px; display: flex;
}
.insights {
  margin-top: 25px; border-radius: 5px; font-size: 18px;
  padding-left: 25px; padding-top: 25px; font-weight: 300;
  display: flex; flex-direction: column;
  width: 350px; height: 140px; background: rgba(255, 255, 255, 0.61);
}
.week {
  font-family: 200; color: grey; font-size: 14px; text-transform: uppercase;
}
:root {
  --font: rgb(50, 15, 107);
  --purple: rgb(123, 66, 255, 0.6);
  --blue: rgb(61, 168, 255, 0.6);
  --yellow: rgba(255, 232, 155, 0.6);
}
.iBtn {
  width: 375px; background: var(--purple); height: 35px; margin-left: -25px; border-radius: 2.5px;
}
.flexMain {
  display: flex; flex-direction: column; justify-content: space-between; height: 100%;
}
.headAvatar {
  width: 50px; height: 50px; background: red; margin-right: 10px;
}